@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  overflow: auto;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #111;
  color: #eee;
  letter-spacing: 1.5px;
}

.container {
  max-width: 1100px;
  margin: 20px auto;
  /* overflow: auto; */
  min-height: 300px;
  /* border: 1px solid red; */
  padding: 30px;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.logo {
  display: flex;
  justify-content: center;
  height: 100px;
  width: auto;
  margin: 30px auto;
}
.nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.header img {
  max-width: 108px;
}

li {
  margin: 20px 10px;
  padding: 0 10px;
  list-style: none;
}

a {
  color: #8a6b08;
  text-decoration: none;
}

a:hover,
a:active {
  color: #d65f1a;
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.home img {
  max-width: 100%;
}

.home_text {
  margin: 30px auto;
  padding: 10px;
  text-align: center;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 54px auto;
  color: #d65f1a;
}

